import React from 'react';
import {AnimatedOnScroll} from "react-animated-css-onscroll";

import Bipex from '../../assets/Bn.png'

function Bn() {
    return(
        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut" animationInDuration={2000}>
        <div>
            <div style={{display:'inline-flex', border:'1px solid'}}>
                <div style={{width: '50vw', justifyItems: 'flex-end'}}>
                <img src={Bipex} style={{
                    borderTopRightRadius:'70px',
                    borderTopLeftRadius:'70px',
                    borderBottomLeftRadius:'70px',
                    borderBottomRightRadius:'70px'}} />
                </div>
                <div>
                    <h1 style={{color:'#329'}}>주요 업무</h1>
                </div>
                <div style={{paddingBottom:'20px'}}>
                    <h3 style={{textAlign: 'left'}}>- ERP개발 및 운영</h3>
                    <h3 style={{textAlign: 'left'}}>- 그룹웨어 시스템 운영 (IBM Lotus Notes Solution)</h3>
                </div>
            </div>
            
            <div>
                <h1 style={{color:'#329'}}>프로젝트</h1>
            </div>
            <div>
                <h3>▷ Cargo System</h3>
                <p>- B/L관리, Order관리, 세금계산서관리 프로그램 개발 </p>
                <p>C#, ASP.NET, MSSQL, DevExpress Component</p>

                <h3>▷ CCM ( 골프장 통합관리 - 레이포드CC )</h3>
                <p>- 직원관리, 캐디관리, 회원관리, 예약/문자발송 시스템 개발 </p>
                <p>VB.NET, Oracle, Farpoint Spread</p>

                <h3>▷ 자산관리 시스템</h3>
                <p>- 전사 통합 자산관리 (자산등록, 자산지급, 자산반납, 자산이동, 자산조회(QR Code) ) </p>
                <p>VB.NET, Oracle, Farpoint Spread</p>
            </div>
        </div>
        </AnimatedOnScroll>
    );
}

export default Bn;