import React from 'react';
import {AnimatedOnScroll} from "react-animated-css-onscroll";

import Sj from '../../assets/Sejung.png'

function Sjinc() {
    return(
        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut" animationInDuration={2000}>
        <div>
            <div><img src={Sj}/></div>
            <div>
            <h1>주요 업무</h1>
            </div>
            <div>
                <h3>- ERP개발 및 운영</h3>
                <h3>- 그룹웨어 시스템 운영 (IBM Lotus Notes Solution)</h3>
            </div>
            <div>
            <h1>프로젝트</h1>
            </div>
            <div>
                <h3>- Cargo System</h3>
                <h3>- CCM ( 골프장 통합관리 - 레이포드CC )</h3>
                <h3>- 자산관리 시스템</h3>
            </div>
        </div>
        </AnimatedOnScroll>
    );
}

export default Sjinc;